@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
*{
    padding: 0;
    margin:0;
    box-sizing: border-box;
}

body{
    background-color: #003566;
    width: 100%;
    height: 100%;
    text-align: center;
   
}

.info{
    font-size: 6rem;
}

.signin{
    margin-top: 150px;
}

.signin-btn{
    width: 120px;
    height: 70px;
    font-size: 1rem;
    background-color: yellow;
    border-radius: 20px;
    border: none;
}

.info{
    font-family: 'Fredoka One', cursive;
    color:yellow ;
    opacity: 0.4;
}

.locker{
    color: rgb(180, 89, 89);
}


.chat{
    text-align: center;
    width: 80%;
    height: 80%;
    margin: auto;
    border: 1px solid black;
    color:white;
    font-size: 1rem;
    position: relative;
    background-color: #001d3d;
    overflow-y:scroll;
    font-family: Josefin Sans;
    
}

.cont-received{
    display: flex;
    align-items: center;
}

.cont-sent{
    display: flex;
    align-items: center;
    justify-content: right;
}


.img{
    border-radius: 50%;
    width: 40px;
    margin: 10px;
}


.cont{
    display: flex;
    align-items: center;
}

.received-text{
    background-color: #97acbe;
    padding: 6px;
    border-radius: 50px;
    color: black;
}
.sent-text{
    background-color: #1e8df5;
    padding: 6px;
    border-radius: 50px;
}


.container{
    align-items: center;
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}

.signout-btn-cont{
    background-color: rgb(43, 43, 43);
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: right;
    width: 80%;
    margin: auto;


}

.chat-cont{
  width: 100%;
  height: 100%;
  text-align: center;
}
.signout{
    margin: auto;
    background-color: rgb(192, 97, 97);
    width: 80px;
    height: 44px;
    border: none;
    font-size: 18px;
    border-radius: 20px;
    margin-right: 10px;
}

.send-message{
    bottom: 0; 
    width: 100%;
    display: flex;
    border: 1px solid black;
    background-color: #001d3d;
    padding: 5px;
    width: 80%;
    margin: auto;
}

input{
    background-color: transparent;
    border: 1px solid black;
    border-style: none;
    height: 25px;
    width: 100%;
    font-size: 1.2rem;
}

input:focus{
    outline: none;
}

.input-style{
    padding: 10px;
    border-radius: 50px;
    background-color: #ffd60a;
    width: 100%;
}
